.search-box {
  text-align: center;
  max-width: 80%;
  margin: 0 auto;
  padding: 30px 0;
}

.search-box .btn {
  background: red;
  color: #fff;
  padding: 5px 30px;
  font-size: 1.5rem;
  border: 0;
  border-radius: 10px;
  cursor: pointer;
}

.search-box .btn:active,
.search-box .btn:hover {
  background: green;
}

.search-box .action {
  margin-top: 15px;
  color: red;
}

.search-box .action.success {
  color: green;
}

.search-box .result {
  border: 1px solid green;
  padding: 10px 20px;
  border-radius: 10px;
  margin-top: 15px;
}
